.Footer{
    width: 100%;
    height: 100vh;
    position: relative;
}
.logo-footer{
    position: relative;
    top: 10%;
    width: 20vw;
}
.hoja-tobaco-foot{
    width: 30vw;
    position: absolute;
    bottom:45%;
    left: 35%;
}
.Foot{
    position: absolute;
    width: 100%;
    height: 50%;
    bottom:0;
    background: rgb(120,32,150);
    background: linear-gradient(68deg, rgba(120,32,150,1) 31%, rgba(109,33,147,1) 44%, rgba(95,35,143,1) 52%, rgba(84,37,139,1) 61%, rgba(13,46,117,1) 88%);
}
.Foot h1{
    font-weight: bold;
    font-size: 5vw;
    letter-spacing: 8px;
    color: #fff;
    position: absolute;
    margin: 0;
    width: 20%;
    left:40%;
    top: 5%;
}
.Foot a img{
    width: 6vw;
    position: relative;
    top: 40%;
    transition: width 0.1s ease-in-out;
}
.Foot a img:hover{
    width: 6.2vw;
}
.Foot h4{
    font-size: 1vw;
    font-weight: bold;
    letter-spacing: 8px;
    color: #fff;
    position: absolute;
    width: 20%;
    left:40%;
    bottom: 5%;
}
@media only screen and  (max-width:700px){
    .logo-footer{
        position: relative;
        top: 10%;
        width: 60vw;
    }
    .hoja-tobaco-foot{
        width: 30vw;
    }
    .Foot h1{
        font-size:12vw;
        width: 40%;
        left:30%;
    }
    .Foot h4{
        font-size: 3vw;
    }

}